.sidebar-links {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-family: var(--main-font);
}

.sidebar-links--link {
  text-decoration: none;
  color: var(--main-text);
  font-size: 1.3rem;
  margin: var(--margin-small) 0px;
}

.sidebar-links--link:hover {
  color: var(--main-accent);
}

.sidebar-links--accent {
  color: var(--main-accent);
}

@media screen and (max-width: 600px) {
  .sidebar-links {
    flex-direction: row;
  }

  .sidebar-links--link {
    margin-right: 10px;
    padding: 10px;
    border-radius: var(--margin-small);
    /* background-color: var(--secondary-bg); */
    border: 0.5px solid;
    font-size: 1rem;
  }
}
